import { useState } from "react";
import {useParams} from "react-router-dom";
import Cookies from "universal-cookie";

import {WithContext as ReactTags} from "react-tag-input";
import CATEGORIES from "./categories";




const suggestions = CATEGORIES.map(category =>{
    return {
        id:category, 
        text:category,
    };
});
const KeyCodes = {
    comma: 188, 
    enter: 13
};
const delimiters = [KeyCodes.comma, KeyCodes.enter];
export function Single(){
    const [tags, setTags] = useState([
        { id: 'newFormations', text: 'Nouvelle formation' },
        { id: 'India', text: 'India' },
        { id: 'Vietnam', text: 'Vietnam' },
        { id: 'Turkey', text: 'Turkey' }
      ]);
    
      const handleDelete = (i) => {
        setTags(tags.filter((tag, index) => index !== i));
      };
    
      const handleAddition = (tag) => {
        setTags([...tags, tag]);
      };
    
      const handleDrag = (tag, currPos, newPos) => {
        const newTags = tags.slice();
    
        newTags.splice(currPos, 1);
        newTags.splice(newPos, 0, tag);
    
        // re-render
        setTags(newTags);
      };
    
      const handleTagClick = (index) => {
        console.log('The tag at index ' + index + ' was clicked');
      };

    const [checkCookie, setCheckCookie] = useState('');

    const handleClickConnect = () => {
        const cookie = new Cookies();
        // cookie.set('role', 'admin');
        cookie.set('role', 'admin', { path: '/' })
        setCheckCookie('admin');


    }
    
    const {id} = useParams();
    // const getCookie = Cookies.get('role');
    // throw new Error("Page inexistante !");
    return (
        <>
            <div className={checkCookie === 'admin' ? 'hidden' : 'mx-32 my-20'}>
                <h1>Article {id}</h1>
                <button className="px-5 py-4 border border-5 border-indigo-700" onClick={handleClickConnect}>Connexion</button>
                
            </div>
            <div className="mx-20 md:mx-32 my-20">
                {checkCookie === 'admin' ? 
                    (
                        <>
                        <h2 className="font-black text-2xl md:text-6xl mb-8">Ajouter un article</h2>
                        <form method="post">
                            <div className="flex flex-col mb-8">
                                <label>Titre de l'article</label>
                                <input type='text' name="titre"/>
                            </div>
                            <div className="flex flex-col mb-8">
                                <ReactTags 
                                    type='text' 
                                    name="category" 
                                    tags={tags}
                                    suggestions={suggestions}
                                    delimiters={delimiters}
                                    handleDelete={handleDelete}
                                    handleAddition={handleAddition}
                                    handleDrag={handleDrag}
                                    handleTagClick={handleTagClick}
                                    inputFieldPosition="bottom"
                                    labelField='name'
                                    autocomplete
                                />
                            </div>
                            <div className="flex flex-col mb-8">
                                <label>Catégorie de l'article</label>
                                <input type='text' name=""/>
                            </div>
                            <button className="px-5 py-4 border border-5 border-indigo-700">Créer un article</button>
                        </form>
                        </>
                    ) : ''}

                
            </div>
        </>
    )
}